<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-nav">
            <DatePicker type="date" placeholder="Select date" style="width: 200px" @on-change="changeDate" ></DatePicker>
            <MenuItem name="1" @click.native="loadData">
              <Icon type="ios-paper" ></Icon>
              刷新
            </MenuItem>
          </div>
        </Menu>
      </Header>
      <Content :style="{padding: '0 50px'}">
        <Card>
          <div style="min-height: 820px;">
            <Table border :columns="VoiceColumns" :data="files"></Table>
            <Page :total="total" :page-size ="size" :current="page+1" show-elevator @on-change="handlePage"/>
          </div>
        </Card>
      </Content>
      <Footer class="layout-footer-center">版权所有&copy; 蛙声科技</Footer>
    </Layout>
    <Modal
      v-model="modal_remove"
      title="删除框"
      @on-ok="ok_remove"
      @on-cancel="cancel_remove">
      <p>真的要删除吗，不在考虑一下？</p>
    </Modal>
    <Modal
      v-model="modal_update"
      title="修改框"
      @on-ok="ok_update"
      @on-cancel="cancel_update">
      <Row>
        <Icon type="ios-browsers-outline" size="20"/>ID：{{fileId}}
      </Row>
      <br>
      <Row>
        <Icon type="ios-folder-outline" size="20" />文件名：
        <Input v-model="filename" placeholder="Enter something..." style="width: 300px" />
      </Row>
      <br>
      <Row>
        <Icon type="ios-hand-outline" size="20"/>标签：
        <Input v-model="filelabel" placeholder="Enter something..." style="width: 300px" />
      </Row>
    </Modal>
    <Modal
      v-model="modal_play"
      title="播放框"
      @on-ok="ok_play"
      @on-cancel="cancel_play">
      <div class="demo-spin-article">
        <Row>
          <Col span="8"><Icon type="ios-browsers-outline" size="20"/>ID：{{fileId}}</Col>
          <Col span="8"> <Icon type="ios-folder-outline" size="20" />文件名：{{filename}}</Col>
          <Col span="8"> <Icon type="ios-hand-outline" size="20"/>标签：{{filelabel}}</Col>
        </Row>
        <br>
        <br>
        <Row>
          <Spin fix fix v-if="playflag">
            <Icon type="ios-loading" size=35 class="demo-spin-icon-load"></Icon>
            <div>录音播放中...</div>
          </Spin>
          <audio v-bind:src="fileurl" id="audio"></audio>
        </Row>
        <br>
        <br>
        <Row>
          播放：<i-switch @on-change="play"></i-switch>
        </Row>
      </div>
    </Modal>
  </div>
</template>
<script>
  function addZero(v) {
    return v < 10 ? `0${v}` : v;
  }
  export default {
    props: {
      Audio: {
        type: Object,
      },
    },
    data () {
      return {
        datetime:"2000-12-01",
        fileId:0,
        filename:"",
        filelabel:"",
        fileurl:"https://ws-record-backup.oss-cn-beijing.aliyuncs.com/05-9%E6%9C%88-2018/kws-nihaoxiaowa-0.2m-1-fast-wh-22-1-67d959f9-ebb6-49b8-812a-a98bc868a14e.wav?Expires=1536301310&OSSAccessKeyId=TMP.AQEXlFzwf72uh_RvV429im6Nqc2phWa-sWvhMP-6TZNJvTkkbH3KnJwejDgfMC4CFQDtDhW-IDTnxIFtvEuv7MbmR0eAkQIVAJZLkOHFMcX5WB-bJe4jzD_qa8xd&Signature=TLxND4RSxHNzIexKSaFq64pYZ7k%3D",
        total:100,
        size:10,
        page:0,
        playflag:false,
        files: [],
        modal_remove:false,
        modal_update:false,
        modal_play:false,
        value: 0,
        auObj: null,
        currentminutetime: 0,
        currentsecondtime: '00',
        allminutetime: 0,
        allsecondtime: '00',
        VoiceColumns: [
          {
            title: 'ID',
            key: 'id',
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                h('strong', params.row.id)
              ]);
            }
          },
          {
            title: '文件名',
            key: 'name'
          },
          {
            title: '标签',
            key: 'label'
          },
          {
            title: '操作',
            key: 'action',
            width: 200,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '10px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row)
                    }
                  }
                }, '试听'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '10px'
                  },
                  on: {
                    click: () => {
                      this.update(params.row)
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row)
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
      }
    },
    created(){
      this.loadpage();
    },
    mounted() {
      this.auObj = document.getElementById('audio');
      this.auObj.ontimeupdate = () => {
        this.value = (this.auObj.currentTime / this.auObj.duration) * 100;
        this.currentminutetime = (Math.round(this.auObj.currentTime) - (Math.round(this.auObj.currentTime) % 60)) / 60;
        this.currentsecondtime = addZero(Math.round(this.auObj.currentTime) % 60);
        this.allminutetime = (this.auObj.duration - (this.auObj.duration % 60)) / 60;
        this.allsecondtime = addZero(Math.round(this.auObj.duration) % 60);
      };
    },
    methods: {
      loadpage(){
        let _self = this;
        this.$axios.get("/voice/page", {
          params: {
            datetime:_self.datetime,
            page:_self.page,
            size:10,
          }
        }).then(result => {
          console.log(result);
          _self.files=result.data.content;
          console.log(_self.files);
          _self.total=result.data.totalElements;
        })
          .catch(err => {});
      },
      updates(){
        let _self = this;
        this.$axios.get("/voice/update", {
          params: {
            id:_self.fileId,
            name:_self.filename,
            label:_self.filelabel,
          }
        }).then(result => {
          console.log(result);
          _self.loadpage();
          this.$Message.success('修改成功！');
        })
          .catch(err => {});
      },
      delete(){
        let _self = this;
        this.$axios.get("/voice/delete", {
          params: {
            id:_self.fileId,
          }
        }).then(result => {
          console.log(result);
          _self.loadpage();
          this.$Message.success('删除成功！');
        })
          .catch(err => {});
      },
      show (row) {
       this.fileId=row.id;
       this.filename=row.name;
       this.filelabel=row.label;
       this.fileurl=row.uri;
       console.log("show:"+this.fileId);
       this.modal_play=true;
       this.loadpage();
      },
      update(row){
        this.fileId=row.id;
        this.filename=row.name;
        this.filelabel=row.label;
        console.log("show:"+this.fileId);
        this.modal_update=true;
      },
      remove(row) {
        this.fileId=row.id;
        this.filename=row.name;
        this.filelabel=row.label;
        console.log("show:"+this.fileId);
        this.modal_remove=true;
      },
      changeDate(date){
        this.page=0;
        this.datetime=date;
        console.log("show:"+this.datetime);
        this.loadpage();
      },
      loadData(){
        console.log("flash");
        this.$Message.success("刷新成功");
        this.loadpage();
      },
      handlePage(value){
        this.page=value-1;
        console.log((this.page));
        this.loadpage();
      },
      ok_remove(){
        this.delete();
      },
      cancel_remove(){
        this.modal_remove=false;
      },
      ok_update(){
        this.updates();
      },
      cancel_update(){
        this.modal_update=false;
      },
      ok_play(){
        this.modal_play=false;
      },
      cancel_play(){
        this.modal_play=false;
      },
      play(){
        console.log("play:"+this.fileurl);
        if(this.playflag==false){
          this.playflag=true;
          this.auObj.play();
        }else{
          this.playflag=false;
          this.auObj.pause();
        }
      }
    }
  }
</script>
<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }
  .layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
  .layout-footer-center{
    text-align: center;
  }
  .demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
  }
  @keyframes ani-demo-spin {
    from { transform: rotate(0deg);}
    50%  { transform: rotate(180deg);}
    to   { transform: rotate(360deg);}
  }
  .demo-spin-col{
    height: 100px;
    position: relative;
    border: 1px solid #eee;
  }
</style>



